/*
 * App Global CSS
 * ----------------------------------------------------------------------------
 * Put style rules here that you want to apply globally. These styles are for
 * the entire app and not just one component. Additionally, this file can be
 * used as an entry point to import other CSS/Sass files to be included in the
 * output CSS.
 * For more information on global stylesheets, visit the documentation:
 * https://ionicframework.com/docs/layout/global-stylesheets
 */

/* Core CSS required for Ionic components to work properly */
@import "~@ionic/angular/css/core.css";

/* Basic CSS for apps built with Ionic */
@import "~@ionic/angular/css/normalize.css";
@import "~@ionic/angular/css/structure.css";
@import "~@ionic/angular/css/typography.css";
@import '~@ionic/angular/css/display.css';

/* Optional CSS utils that can be commented out */
@import "~@ionic/angular/css/padding.css";
@import "~@ionic/angular/css/float-elements.css";
@import "~@ionic/angular/css/text-alignment.css";
@import "~@ionic/angular/css/text-transformation.css";
@import "~@ionic/angular/css/flex-utils.css";

ion-header{
	height: 35%;
	color:white;
	background-color:#ff304b;
	position: absolute;
	z-index: 1 !important;
	ion-title{
		position:relative;
		top:20%;
		font-size:23px;
	}
}
.dashboard-header{
	margin:10px 15px 5px 15px;
	height:20%;
	align-items:center;
	.title{
		font-size:25px;
		font-weight:bold;
	}

}


.dashboard-content{
	--background: #ebf2fb;
	padding-top:10px;
	position:absolute;
	ion-toolbar{
		margin-top:-10px;
		height:30%;
		position:relative;
		z-index:1;
		top:-5%;
		padding:15px;
	}
	ion-grid {
		position:relative;
		justify-content: center;
		margin-top:-25px
	}
	.title{
		font-size:25px;
		font-weight:bold;
		padding-bottom:0px;
	}
	.text{
		color:#d0d0d1;
		font-size: 13px;
		padding-top:12px;
		margin: 0px;
		line-height: 0.4cm;
	}
}
.auth-content{
	ion-item{
		position:relative;
		top:60%;
		height:50%;
	}
	ion-row{
		margin-bottom:30px;
		justify-content: center;
	}
	ion-button{
		width:70%;
		height:50px;
		--border-radius:50px;
		font-size:15px;
	}
}


.card-holder{
	z-index: 2;
	margin-top:10%;
	position: relative;
    @media only screen and (min-width:1190px) {
		top:5%;
	}
	top:30%;
	display: flex;
	align-items: center; 
	justify-content: center;
	height:80%;
	ion-card{
		width: 70%;
		height: 100%;
		offset: 4;
		padding-top:50px;
		padding-bottom:10px;
		font-size: 16px;
		color: #c0bfbf;
		ion-input{
			margin-left:20%;
			margin-top:40px;
			border-bottom: 1px solid #edefee; 
			text-align: left;
			width:70%; 
		}
	}
	#partitioned {
		padding-left: -15%;
		letter-spacing: 12px;
		border: 0;
		background-image: linear-gradient(to left, black 40%, rgba(255, 255, 255, 0) 0%);
		background-position: bottom;
		background-size: 50px 1px;
		background-repeat: repeat-x;
		background-position-x: 35px;
		width: 400px;
		min-width: 400px;
	
	  }
	#divInner{
		left: 0;
		position: sticky;

	}
	#divOuter{
		width: 360px; 
		overflow: hidden;
	
	}
}
.flash-card{
	height:180px;
	width: 45%;
	//margin:6px 8px 2px 10px;
	display:flex;
	flex-direction:column;
	background-color:rgba(255,255,255);
	box-shadow:2px 0px 2px 0px lightgray;
	text-align: left;
	.text
	{
	  font-size:14px;
	  color:black;
	  font-weight:800;
	}
	.small-text{
		font-size:10px;
		color:gray;
		//
	}
}

.header{
	height: 35%;
	background-color:white;
}
.sub-title{
	float:left;
	font-size:20px;
	padding:5px;
	font-weight:900;
}

.my-custom-modal-css {
	height: 40%;
	top: 25%;
	width:90%;
	position: absolute; 
	display: block; 
	margin:10px 15px 0px 15px;
	border:1px solid rgba(233, 231, 231, 0.9);
	box-shadow: 1px 1px 1px 1px #e6e4e4; 
	border:1px solid green;
	.header{
		padding:20px 20px 20px 20px;
		background-color:green;
		height:30%;
		color:white;
	}
	.content{
		--background: white;
		align-items:center !important;
		justify-content:center !important;
	}
     ion-backdrop{
		 background-color: white;
		 border-radius: 15px; 
		 border:1px solid red;
		 padding:2px;
		 visibility: hidden;
	 }
 }
 .medium-text{
	 font-size:13px;
	 color:gray;
 }
 .small-text{
	font-size:11px;
	color:gray;
}
.text-danger{
  color:#ff304b;
  font-size:14px;
 &::before{
	 content: '-';
 }
}
 .text-success{
	color:green;
	font-size:14px;
   &::before{
	   content: '+';
   }

}
.active-link{
	color:blue;
}

ion-footer{
	margin-left:30px;
	margin-right:10px;
	padding:10px 20px 10px 10px;
	font-size:20px;
	height:8%;
	position: relative;
}
.input-container {
	position: relative;
	height: 266px; 
	overflow: scroll;
	width: 80%;
	z-index: 9999;
	margin-left: 11%;
	
  }
 .gain-grid{
	margin:0px 10px 5px 10px;
	padding-top:0px;
  ion-row{
	margin:0px;
	padding: 0px;
  }
  ion-col{
	margin:0px;
	padding: 0px;
	.div-card{
		border-radius: 50%;
		border:1px solid rgba(128,128,128,0.1);
		height:50px;
		width:50px; 
		text-align: center;
		align-items: center;
		margin:5px;
		img{
			height:30px;
			width:30px;
			margin-top:25%;
		}
	}
  }
 }
